<template>
    <div>
        <div class="list" id="lists">
            <div class="head">
                <div class="newadd">新增设备</div>
                <div class="delet" @click="delet" style="cursor:pointer">X</div>
            </div>
            <div>
                <div class="conter">
                    <span class="spa1"  style="width:90px;">gps伪IP</span>
                    <el-input style="width:90%;" v-model="input" placeholder="请输入内容"></el-input>
                </div>
                <div class="conter">
                    <span class="spa2" style="width:80px;">设备编号</span>
                    <!-- <div class="selet">
                        <el-select v-model="value11" multiple collapse-tags style="margin-left: 20px;" placeholder="请选择">
                            <div class="posi">
                                    <el-option popper-class="optios" style="width:100%;" v-for="item in info" :key="item.fnum"
                                    :label="item.nodename" :value="item.fnum">
                                </el-option>
                            </div>
                        </el-select>
                    </div> -->
                    <div class="input1">
                        <div class="input2">
                            <input type="text" v-model="selectvalue" @click="inputclick">
                            <div class="selet" v-show="selethide">
                                <ul v-for="(item,index) in info" :key="index" class="listul">
                                    <li @click="seletclick(index,item.fnum,item.nodename)" class="lilist">
                                           <span>{{item.nodename}}</span>
                                           <span class="gou" v-show="imghide[index]">
                                              <img src="../../assets/icon/check.png" alt="">
                                           </span>
                                        </li>
                                    
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div  class="conter conters">
                <div @click="submit">提交</div>
                <div @click="delet">取消</div>
            </div>
        </div>

    </div>
</template>
<script>
import { TXnodesave, TXnodeadd, TXnodedelet } from "../../api/api";
export default {
  data() {
    return {
      imghide: [],
      selethide: false,
      selectvalue: [],
      total: 0,
      page: 1,
      size: 10,
      info: [],
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value5: [],
      value11: [],
      input: ""
    };
  },
  created() {
    console.log(this.$store.state.allnums);
    this.info = this.$store.state.allnums;
    this.addTo();
  },
  mounted() {
    let length = this.$store.state.allnums.length;
    // let imghide=[]
    for (let i = 0; i < length; i++) {
      this.imghide[i] = false;
    }
  },
  methods: {
    inputclick() {
      this.selethide = !this.selethide;
    },
    seletclick(index, vul, fun) {
      //   console.log(this.imghide)
      //   this.imghide[index]=!this.imghide[index]
      this.$set(this.imghide, index, !this.imghide[index]);
      console.log(index);
      let value = this.value11;
      if (value.indexOf(vul) == -1) {
        this.value11.push(vul);
        this.selectvalue.push(fun);
      } else {
        this.selectvalue.splice(value.indexOf(vul), 1);
        this.value11.splice(value.indexOf(vul), 1);
      }
      console.log(this.selectvalue);
      console.log(this.value11);
    },
    // submit(){
    //     console.log(this.value11)
    // },
    newadd() {},
    delet() {
      this.$store.state.TXhides = false;
    },
    // 添加TX设备
    submit() {
      let that = this;
      if (this.input == "") {
        that.$message({
          message: "gps伪IP不能为空",
          type: "warning"
        });
        return;
      }
      if (this.value11 == "") {
        that.$message({
          message: "设备编号不能为空",
          type: "warning"
        });
        return;
      }
      let para = {
        pretendIp: this.input,
        nodeIds: this.value11.join()
      };
      TXnodeadd(para).then(data => {
        if (data.code == -1) {
          this.$message({
            message: "提交失败",
            type: "warning"
          });
        }
        if (data.code == 1) {
          that.$message({
            message: "添加成功",
            type: "success"
          });
        }
      });
    },
    //分页

    handleSizeChange(val) {
      // alert(`每页 ${val} 条`);
      this.size = val;
      this.addTo();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.addTo();
    }
  }
};
</script>
<style>
.selet .el-select-dropdown {
  margin-top: 115px !important;
}
.selet input {
  width: 359%;
  height: 30px !important;
  overflow: hidden;
  border: 1px solid #bfcbd9;
}
</style>

<style scoped>
.selet {
  width: 100%;
  height: 40px;
  overflow: hidden;
}
.list {
  margin: 0 auto;
  height: 60vh;
  width: 22%;
  box-shadow: 0px 0px 8px 8px #ccc;
  background: #fff;
  padding: 20px;
  position: relative;
}

.head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

ul {
  height: 4vh;
  line-height: 6vh;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #ccc;
}

li {
  list-style: none;
  width: 33%;
  text-align: center;
}

.newadd {
  /* background: #2281de; */
  color: #000;
  border-radius: 4px;
  padding: 4px 10px;
}

.blue {
  color: #2281de;
}

.delet {
  border-radius: 4px;
  padding: 4px 10px;
}

.footer-bottom {
  width: 20%;
  position: absolute;
  bottom: 0;
}
.conter {
  display: flex;
  flex-direction: row;
  /* border:1px solid black; */
  align-items: center;
  margin: 20px 0;
  height: 40px;
}
.conters {
  position: absolute;
  bottom: 0;
  width: 90%;
  margin: 0 auto;
  justify-content: center;
}
.conters > div {
  width: 50px;
  text-align: center;
  /* border:1px solid red; */
  background: #2281de;
  color: #fff;
  height: 30px;
  line-height: 30px;
  border-radius: 4px;
  margin: 0 40px;
  cursor: pointer;
}
.input1 {
  width: 100%;
}
.input2 {
  position: relative;
  width: 96%;
  margin-left: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
}
.input2 > input {
  width: 100%;
  height: 28px;
  border: 0;
}
.selet {
  border: 1px solid #ccc;
  height: 240px;
  position: absolute;
  overflow-y: scroll;
  top: 38px;
}
.lilist {
  padding: 0 20px;
  width: 100%;
  height: 100%;
  /* border:1px solid red; */
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.lilist:hover {
  background: #ccc;
}
::-webkit-scrollbar {
  width: 0px;
}
.gou > img {
  width: 18px;
}
.listul {
  border-bottom: 0;
}
</style>


