<template>
    <div>
        <div class="list">
            <div class="head">
                <div class="newadd" style="cursor:pointer" @click="add">新增</div>
                <div class="delet" @click="delet" style="cursor:pointer">X</div>
            </div>
            <div>
                <ul>
                    <li>gps伪IP</li>
                    <li>节点编号</li>
                    <li></li>
                </ul>
                <ul v-for="(item,index) in info" :key="index">
                    <li class="blue">{{item.pretendIp}}</li>
                    <li>{{item.nodeId}}</li>
                    <li class="blue" style="cursor:pointer" @click="delets(item.nodeId)">删除</li>
                </ul>
            </div>
            <div>
                <el-footer class="footer-bottom">
                    <!--工具条-->
                    <el-col :span="24" class="toolbar-bot">
                        <span class="demonstration" style="float: left; margin-top: 10px;">共:{{total}}条</span>
                        <el-pagination style="cursor:pointer" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                            :current-page="currentPage1" :page-size="size" layout="prev, pager, next, jumper" :total="total">
                        </el-pagination>
                    </el-col>
                </el-footer>
            </div>
        </div>
    </div>
</template>
<script>
import { TXnodesave, TXnodedelet } from "../../api/api";
export default {
  data() {
    return {
      total: 0,
      page: 1,
      size: 10,
      info: []
    };
  },
  created() {
    this.addTo();
  },
  methods: {
    add() {
      this.$store.state.TXhides = true;
    },
    newadd() {
      this.$store.state.TXhide = true;
    },
    delet() {
      this.$store.state.TXhide = false;
    },
    delets(index) {
      let that = this;
      this.$confirm("继续删除操作吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let para = {
            nodeId: index
          };
          TXnodedelet(para).then(data => {
            if (data.code == -1) {
              this.$message({
                message: "删除失败",
                type: "warning"
              });
            }
            if (data.code == 1) {
              that.$message({
                message: "删除成功",
                type: "success"
              });
              that.addTo();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    addTo() {
      let that = this;

      let para = {
        start: (this.page - 1) * 10,
        size: 10,
        fnum: ""
      };
      TXnodesave(para).then(data => {
        if (data.code == -1) {
          this.$message({
            message: "提交失败",
            type: "warning"
          });
        }
        if (data.code == 1) {
          that.total = data.msg.total;
          that.info = data.msg.rows;
          that.$store.state.info = data.msg.rows;
        }
      });
    },
    //分页

    handleSizeChange(val) {
      // alert(`每页 ${val} 条`);
      this.size = val;
      this.addTo();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.addTo();
    }
  }
};
</script>
<style scoped>
.list {
  margin: 0 auto;
  height: 60vh;
  width: 22%;
  box-shadow: 0px 0px 8px 8px #ccc;
  background: #fff;
  padding: 20px;
  position: relative;
}

.head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

ul {
  height: 4vh;
  line-height: 6vh;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #ccc;
}

li {
  list-style: none;
  width: 33%;
  text-align: center;
}

.newadd {
  background: #2281de;
  color: #fff;
  border-radius: 4px;
  padding: 4px 10px;
}

.blue {
  color: #2281de;
}

.delet {
  border-radius: 4px;
  padding: 4px 10px;
}

.footer-bottom {
  width: 20%;
  position: absolute;
  bottom: 0;
}
</style>


