        ],
        yAxis: [
          {
            type: "value",
            name: "（°C）",
            min: this.tmp12min,
            max: this.tmp12max,
            axisLabel: {
              formatter: "{value} "
            }
          },
          {
            type: "value",
            name: "（%）",
            min: 0,
            max: 100,
            axisLabel: {
              formatter: "{value} "
            }
          }
        ],

        series: [tmp1, hum1]
      };
      // console.log(allnodedd);
      //  console.log(option)
      this.myChart12.setOption(option12);
      // console.log(this.allnodedd)
    },
    drawChart12() {
      var that = this;
      if (this.value3 == "") {
        this.value3 = new Date(new Date().getTime() - 24 * 60 * 60 * 1000);

        this.value4 = new Date(new Date().getTime());
        that.value3 =
          !that.value3 || that.value3 == ""
            ? ""
            : util.formatDate.format(new Date(that.value3), "yyyy-MM-dd hh:mm");
        that.value4 =
          !that.value4 || that.value4 == ""
            ? ""
            : util.formatDate.format(new Date(that.value4), "yyyy-MM-dd hh:mm");
      }

      let para = {
        size: 999,
        page: this.page,
        startTime: this.value3,
        endTime: this.value4,
        vehicleId: this.allnode[11].id
      };
      //查询的结果
      this.loading = true;
      history(para).then(res => {
        this.loading = false;
        var data = res.msg.data;
        if (res.code == 1) {
          if (
            res.msg.tmpAndHumSum.humMax > res.msg.tmpAndHumSum.temperatureMax
          ) {
            this.tmp12max = res.msg.tmpAndHumSum.humMax;
          } else {
            this.tmp12max = res.msg.tmpAndHumSum.temperatureMax;
          }
          if (
            res.msg.tmpAndHumSum.humMin > res.msg.tmpAndHumSum.temperatureMin
          ) {
            this.tmp12min = res.msg.tmpAndHumSum.temperatureMin;
          } else {
            this.tmp12min = res.msg.tmpAndHumSum.humMin;
          }
          this.total = res.msg.total;
          this.displayEchart12(data);
        } else {
          this.open1();
        }
      });
    },
    drawCharts12() {
      this.displayEchart12();
    },
    // 第13个echarts图
    // 画图
    getUsers() {
      this.xianshi = true;
      this.drawChart1();
      this.drawChart2();
      this.drawChart3();
      this.drawChart4();
      this.drawChart5();
      this.drawChart6();
      this.drawChart7();
      this.drawChart8();
      this.drawChart9();
      this.drawChart10();
      this.drawChart11();
      this.drawChart12();
    },
    open8() {
      this.$message({
        showClose: true,
        message: "开始时间要小于结束时间",
        type: "warning"
      });
    },
    getUserst() {
      var that = this;
      if (this.value1 > this.value2) {
        this.open8();
        return;
      }
      that.value1 =
        !that.value1 || that.value1 == ""
          ? ""
          : util.formatDate.format(new Date(that.value1), "yyyy-MM-dd hh:mm");
      that.value2 =
        !that.value2 || that.value2 == ""
          ? ""
          : util.formatDate.format(new Date(that.value2), "yyyy-MM-dd hh:mm");

      this.value3 = this.value1;
      this.value4 = this.value2;

      var that = this;
      let para = {
        page: 1,
        size: 99,
        unitIds: this.filters.name,
        vehicleIds:this.vehicleIds
        // nodename: that.filters.name
      };
      this.allnode = [];

      //查询的结果
      nodeinfo(para).then(res => {
        console.log(res)
        this.allnode = res.msg.rows;
        this.allnums = res.msg.rows;
        this.getUsers();
      });

      // this.drawChart1();
      // this.drawChart2();
      // this.drawChart3();
      // this.drawChart4();
      // this.drawChart5();
      // this.drawChart6();
      // this.drawChart7();
      // this.drawChart8();
      // this.drawChart9();
      // this.drawChart10();
      // this.drawChart11();
      // this.drawChart12();
    },

    jump_info(index) {
      // alert(this.page)
      this.newadds = true;
      var that = this;
      this.num = index;
      let para = {
        size: that.size,
        page: this.page,
        startTime: this.value3,
        endTime: this.value4,
        vehicleId: this.allnode[index - 1].id,
        interval: this.interval
      };
      history(para).then(res => {
        console.log(res);
        var data = res.msg.data;
        if (res.code == 1) {
          this.total = res.msg.total;
          this.all_node = res.msg.data;
        } else {
          this.open1();
        }
        this.page = 1;
        this.size = 15;
      });
    },
    delet() {
      this.newadds = false;
    }
  }
};
</script>
<style>
#shebeis .el-input__inner {
  border: 1px solid #fff;
}
#shebeis .el-input {
  width: 250px;
}
#shebeis .el-select__tags {
  left: -2px;
  margin-top: -3px;
  margin-right: 6px;
