  margin: 3px 4px 3px 6px;
}
#shebeis .el-select__tags span {
  border: 1px solid #fff;
  color: black;
  background: #fff;
  /* opacity:1; */
}
#chart1 > canvas {
  height: 230px;
  top: 40px;
}
#interval_time .el-input__inner {
  width: 45px;
  margin-right: 25px;
}
.toolbar .el-select {
  width: 150px;
}
</style>

<style scoped>
.footer-fenye {
  position: absolute;
  border-top: solid 1px #e4e4e4;
  padding: 10px 1% 10px 1%;
  bottom: 0;
  left: 0;
  width: 98%;
  background: #fff;
  z-index: 100;
}

.chart-container {
  width: 100%;
}
.chart-container .el-col {
  margin-bottom: 20px;
  float: left;
}

.chart1-container {
  /* height:560px; */
  overflow-y: scroll;
  overflow-x: hidden;
}

.el-col {
  background: #fff;
}

.toolbar {
  box-shadow: 0 0 6px 0 #dcdfe6;
}

.newadd {
  position: absolute;
  left: 0px;
  top: 0px;
}

.content {
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.568);
}

.contenter {
  overflow: hidden;
  position: relative;
  width: 85%;
  height: 600px;
  margin: 40px auto;
}

.delet {
  width: 50px;
  height: 50px;
  text-align: right;
  line-height: 50px;
  float: right;
  font-size: 16px;
  font-weight: normal;
  color: #fff;
  cursor: pointer;
}

.jump_info {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 35px;
  top: 6px;
  z-index: 99;
}
.titleNname {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100px;
  height: 20px;
  position: absolute;
  left: 16px;
  top: 4px;
  z-index: 99;
  font-size: 13px;
  font-weight: normal;
  color: #60a0e6;
}
.toolbar-bot .el-pagination {
  float: right;
}
</style>


