      Tmp1max = value;
      if (value === "") {
        return callback(new Error("温度不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (Tmp1min === "") {
            callback();
          } else {
            if (value > Tmp1min) {
              callback(new Error("要小于最大值"));
            } else {
              callback();
            }
          }
        }
      }, 1000);
    };
    var checkTmp1max = (rule, value, callback) => {
      Tmp1min = value;
      // console.log(Tmp1max);
