        return callback(new Error("温度不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (Tmp1max === "") {
            callback();
          } else {
            if (value < Tmp1max) {
              callback(new Error("要大于最小值"));
            } else {
              callback();
            }
          }
        }
      }, 1000);
    };
