      Hum1min = value;
      if (value === "") {
        return callback(new Error("湿度不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (value < 0) {
            callback(new Error("必须大于0"));
          } else {
            if (Hum1max === "") {
              callback();
            } else {
              if (value > Hum1max) {
                callback(new Error("要小于最大值"));
              } else {
                callback();
              }
            }
          }
        }
      }, 1000);
    };
